app-login {
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  background-color: #fff;
  justify-content: space-evenly;
  align-items: center;
}

.login-error-box {
  width: 75%;
  max-width: 16em;
  display: flex;
  flex-direction: column-reverse;
  flex: 2 0 0px;
}
.login-error-box > .alert {
  width: 100%;
}

.login-box {
  width: 75%;
  max-width: 16em;
  flex: 3 0 0px;
}
.login-box__login-button, .login-box__event-input {
  width: 100%;
}
.login-box__processing {
  height: 2.1875em;
  display: flex;
  align-items: center;
  justify-content: center;
}
.login-box__processing > .spinner {
  font-size: 1.2em;
}
.login-box__event-input {
  display: flex;
  align-items: center;
  position: relative;
}
.login-box__event-input > input {
  flex-grow: 1;
  margin-right: 0.5em;
}