.kontokorrent-select {
  border-bottom: 1px solid #fff;
  display: flex;
  align-items: center;
  padding: 0.2em;
  box-sizing: border-box;
  cursor: pointer;
  color: #fff;
  position: relative;
}
.kontokorrent-select__name {
  font-weight: normal;
  font-size: 1.5em;
  padding: 0;
  margin: 0;
  min-width: 6em;
}
.kontokorrent-select__choose {
  cursor: pointer;
  background: transparent;
  border: none;
  font-size: 1em;
  outline: none;
  padding: 0;
  text-decoration: none;
  color: #fff;
  width: 1em;
}
.kontokorrent-select::after {
  border-radius: 3px;
}
.kontokorrent-select:hover::after {
  opacity: 0.08;
}
.kontokorrent-select::after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  content: "";
  opacity: 0;
  transition: opacity 15ms linear;
  box-sizing: border-box;
}
.kontokorrent-select__popup {
  position: absolute;
  min-height: 3em;
  background-color: #fff;
  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2), 0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
  border-bottom-left-radius: 3px;
  border-bottom-right-radius: 3px;
  margin-top: -1px;
  min-width: 15em;
  -webkit-animation: surface-open 0.08s cubic-bezier(0, 0, 0.2, 1);
          animation: surface-open 0.08s cubic-bezier(0, 0, 0.2, 1);
  will-change: transform, opacity;
}
@-webkit-keyframes surface-open {
  0% {
    opacity: 0.6;
    transform: scale(0.9);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
@keyframes surface-open {
  0% {
    opacity: 0.6;
    transform: scale(0.9);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
.kontokorrent-select__buttons {
  text-align: center;
  padding: 0.5em;
}

.kontokorrent-select-wrapper {
  position: relative;
}