.button {
  cursor: pointer;
  background: transparent;
  border: none;
  font-size: 1em;
  outline: none;
  padding: 0;
  text-decoration: none;
  color: #6200ee;
  text-transform: uppercase;
  border-radius: 4px;
  font-size: 0.875em;
  font-weight: 500;
  letter-spacing: 0.08929em;
  height: 2.5em;
  padding: 0 0.5em;
  display: inline-flex;
  justify-content: center;
  align-items: center;
}
.button:hover {
  background-color: rgba(98, 0, 238, 0.04);
}
.button > .material-icons {
  font-size: 1.2em;
  margin: 0 0.2em;
}
.button--raised {
  background-color: #6200ee;
  color: #fff;
  box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
  transition: box-shadow 280ms cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
}
.button--raised:hover {
  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2), 0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
}
.button--raised:hover::after {
  opacity: 0.08;
}
.button--raised::after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  content: "";
  opacity: 0;
  transition: opacity 15ms linear;
  box-sizing: border-box;
}
.button--raised:hover {
  background-color: #6200ee;
}

input {
  outline: none;
  border: none;
  caret-color: #6200ee;
  font-size: 1em;
  line-height: 1.75em;
  box-sizing: border-box;
  border-radius: 0;
  border-bottom: 1px solid rgba(0, 0, 0, 0.42);
  margin-bottom: 0.4em;
}
input:hover {
  border-bottom-color: rgba(0, 0, 0, 0.87);
}

.tip {
  position: absolute;
  background-color: #fff;
  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2), 0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
  border-radius: 3px;
  padding: 0.5em;
  -webkit-animation: surface-open 0.08s cubic-bezier(0, 0, 0.2, 1);
          animation: surface-open 0.08s cubic-bezier(0, 0, 0.2, 1);
  will-change: transform, opacity;
  z-index: 1;
  font-size: 0.8em;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  box-sizing: border-box;
}
@-webkit-keyframes surface-open {
  0% {
    opacity: 0.6;
    transform: scale(0.9);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
@keyframes surface-open {
  0% {
    opacity: 0.6;
    transform: scale(0.9);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
.tip--with-input {
  top: 2em;
  width: 100%;
}

tip-button {
  width: 1em;
  height: 1em;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  color: #6200ee;
  position: relative;
}
tip-button > .material-icons {
  font-size: 1.2em;
}
tip-button:hover::after {
  opacity: 0.08;
}
tip-button::after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  content: "";
  opacity: 0;
  transition: opacity 15ms linear;
  box-sizing: border-box;
}

.alert {
  border: 2px solid;
  border-radius: 3px;
  padding: 0.2em;
  margin: 0.1em;
  box-sizing: border-box;
}
.alert--error {
  border-color: #FF5252;
  background-color: #ffb8b8;
}

body,
button,
input,
ul,
ol {
  font-family: "Roboto", sans-serif;
}

ul,
ol {
  margin: 0;
  padding: 0;
  -webkit-padding-start: 2em;
          padding-inline-start: 2em;
  -webkit-margin-before: 0.5em;
          margin-block-start: 0.5em;
  -webkit-margin-after: 0.5em;
          margin-block-end: 0.5em;
}

body {
  margin: 0;
  padding: 0;
  background-color: #f3f3f3;
}

p {
  margin: 0;
  padding: 0;
  -webkit-margin-before: 1em;
          margin-block-start: 1em;
  -webkit-margin-after: 1em;
          margin-block-end: 1em;
}