.spinner {
  width: 1.1em;
  margin: 0 0.3em;
  height: 1.1em;
  border: 0.14em solid;
  border-radius: 50%;
  border-color: #6200ee;
  border-left-color: rgba(98, 0, 238, 0.04);
  box-sizing: border-box;
  will-change: transform;
  -webkit-animation: spinner-in-progress-animation 0.6s cubic-bezier(0.38, 0.22, 0.63, 0.8) infinite;
          animation: spinner-in-progress-animation 0.6s cubic-bezier(0.38, 0.22, 0.63, 0.8) infinite;
}
@-webkit-keyframes spinner-in-progress-animation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes spinner-in-progress-animation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

