balance-anzeige-element {
  display: block;
}

.balance-anzeige-element__graphic-container {
  width: 100%;
  height: 4em;
  min-width: 4em;
  position: relative;
}
.balance-anzeige-element__text-container {
  text-align: center;
}
.balance-anzeige-element__balance {
  position: absolute;
  font-size: 0.6em;
  width: 100%;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  text-align: center;
  background: #ffffff99;
}
.balance-anzeige-element__name {
  font-size: 0.8em;
}
.balance-anzeige-element__horizontal-line {
  border-top: 0.1em solid #555;
  position: absolute;
  top: 50%;
  width: 100%;
  margin: 0;
  padding: 0;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.balance-anzeige-element__bar {
  background-color: #eee;
  position: absolute;
  top: 50%;
  left: 41.25%;
  width: 17.5%;
  height: 1.9em;
  margin: 0;
  padding: 0;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  transform: scaleY(0);
  transform-origin: top;
  transition: transform 0.3s ease-out, background-color 0.2s ease-in-out;
}
.balance-anzeige-element__bar--positive {
  background-color: #8BC34A;
}
.balance-anzeige-element__bar--negative {
  background-color: #FF5252;
}

balance-anzeige {
  display: flex;
  flex-wrap: wrap;
  padding: 1em 0 0 0;
  justify-content: center;
  background-color: #fff;
}