bezahlungen-view {
  margin: 0 1em;
  display: block;
}

.bezahlungen-group__title {
  font-size: 1em;
  font-weight: normal;
  color: #6200ee;
  -webkit-margin-before: 0.4em;
          margin-block-start: 0.4em;
  -webkit-margin-after: 0.4em;
          margin-block-end: 0.4em;
}
.bezahlungen-group__container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(20em, 1fr));
}
.bezahlungen-group__container > bezahlung-card {
  margin: 0.2em;
}