.kontokorrent-select-list {
  padding: 0;
  margin: 0;
}
.kontokorrent-select-list > li {
  list-style-type: none;
}
.kontokorrent-select-list__entry {
  padding: 0.5em;
  text-decoration: none;
  color: #000;
  width: 100%;
  display: block;
  box-sizing: border-box;
}
.kontokorrent-select-list__entry:hover {
  background-color: rgba(98, 0, 238, 0.04);
}
.kontokorrent-select-list__entry--active {
  background-color: rgba(98, 0, 238, 0.18);
}
.kontokorrent-select-list__entry--active:hover {
  background-color: rgba(98, 0, 238, 0.18);
}