bezahlung-card {
  display: inline-flex;
  align-items: center;
  box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2), 0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12);
  border-radius: 3px;
  padding: 0.4em;
  background-color: #fff;
}

.bezahlung-card__beschreibung {
  flex-grow: 1;
}
.bezahlung-card__personen {
  color: rgba(0, 0, 0, 0.54);
  font-size: 0.875em;
  padding-top: 0.4em;
}
.bezahlung-card__wert {
  color: rgba(0, 0, 0, 0.54);
  margin-left: 1em;
}